.component-embedded-video {
	position: relative;
	padding: {
		top: 35px;
		bottom: 56.25%      // 56.25% used for 16:9 aspect ratio (change to 75% for 4:3 aspect ratio)
	}
	height: 0;
	overflow: hidden;

	iframe {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}